<template>
  <div class="container">
    <h1>Latest comments</h1>
    <!-- CREATE COMMENT HERE -->
    <div class="create-comment">
      <label for="create-comment">Say something...</label>
      <input type="text" id="create-post" v-model="text" placeholder="Comment here...">
      <button v-on:click="createComment">Comment!</button>
    </div>
    <hr>
    <p class="error" v-if="error">{{ error }}</p>
    <div class="comments-container">
      <div class="comment"
        v-for="(comment, index) in comments"
        v-bind:item="comment"
        v-bind:index="index"
        v-bind:key="comment._id"
        v-on:dblclick="deleteComment(comment._id)"
        >
          {{ `${comment.created.getDate()}.${comment.created.getMonth()}.${comment.created.getFullYear()}`}}
          <p class="text">{{ comment.text }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import CommentService from '../CommentService';

export default {
  name: 'CommentComponent',
  data() {
    return {
      comments: [],
      error: '',
      text: ''
    }
  },
  async created() {
    try{
      this.comments = await CommentService.getComments();
    } catch(err){
      this.error = err.message;
    }
  },
  methods: {
    async createComment() {
      await CommentService.insertComment(this.text);
      this.comments = await CommentService.getComments();
    },
    async deleteComment(id) {
      await CommentService.deleteComment(id);
      this.comments = await CommentService.getComments();
    }
  },
}
</script>

